"use client";
import React, { useEffect, useState } from "react";
import Image from "next/image";
import styles from "../ViewContents/ViewContents.module.css";
import { useParams } from "next/navigation";

const ViewContents = () => {
  const { year, month, day, slug } = useParams();
  const [post, setPost] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchPost = async () => {
      try {
        const url = `http://127.0.0.1:8000/api/blog/posts/${year}/${month}/${day}/${slug}/`;
        console.log("Fetching post from URL:", url);

        const res = await fetch(url);
        if (!res.ok) throw new Error(`Failed to fetch post: ${res.status}`);

        const data = await res.json();
        console.log("Fetched data:", data);

        // The API nests the post inside 'post'
        if (data.post) {
          setPost(data.post);
        } else {
          setError("Post data not found in response");
        }
      } catch (err) {
        console.error(err);
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchPost();
  }, [year, month, day, slug]);

  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error: {error}</p>;
  if (!post) return <p>Post not found</p>;

  return (
    <div className={styles.wrapper}>
      <div className={styles.articleContainer}>
        <h1 className={styles.title}>{post.title}</h1>

        {post.image && (
          <div className={styles.imageWrapper}>
            <Image
              src={`https://res.cloudinary.com/dwdhdzva8/${post.image}`}
              alt={post.title}
              fill
              className={styles.image}
              sizes="(max-width: 1200px) 100vw, 1200px"
              priority
            />
          </div>
        )}

        <div className={styles.articleContent}>
          {post.body
            ? post.body.split("\n\n").map((paragraph, idx) => (
                <p key={idx}>{paragraph}</p>
              ))
            : <p>No content available.</p>}
        </div>
      </div>
    </div>
  );
};

export default ViewContents;
